/* Professional UI Styles for Time Saver */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  /* Professional Color Palette */
  --primary-50: #f8fafc;
  --primary-100: #f1f5f9;
  --primary-200: #e2e8f0;
  --primary-300: #cbd5e1;
  --primary-400: #94a3b8;
  --primary-500: #64748b;
  --primary-600: #475569;
  --primary-700: #334155;
  --primary-800: #1e293b;
  --primary-900: #0f172a;
  
  --accent-blue: #3b82f6;
  --accent-green: #10b981;
  --accent-purple: #8b5cf6;
  --accent-orange: #f59e0b;
  --accent-red: #ef4444;
  
  /* Professional Shadows */
  --shadow-sm: 0 1px 2px 0 rgb(0 0 0 / 0.05);
  --shadow-md: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  --shadow-lg: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
  --shadow-xl: 0 20px 25px -5px rgb(0 0 0 / 0.1), 0 8px 10px -6px rgb(0 0 0 / 0.1);
  
  /* Professional Typography */
  --font-sans: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', sans-serif;
  --font-mono: 'JetBrains Mono', 'Fira Code', 'Consolas', monospace;
  
  /* Professional Border Radius */
  --radius-sm: 0.375rem;
  --radius-md: 0.5rem;
  --radius-lg: 0.75rem;
  --radius-xl: 1rem;
}

body {
  font-family: var(--font-sans);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, var(--primary-50) 0%, #fefefe 50%, var(--primary-100) 100%);
  background-attachment: fixed;
  min-height: 100vh;
  color: var(--primary-800);
  line-height: 1.6;
}

code {
  font-family: var(--font-mono);
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 2rem;
  position: relative;
}

/* Professional Card Component */
.card {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  border: 1px solid var(--primary-200);
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-lg);
  overflow: hidden;
}

.card-header {
  padding: 1.5rem 2rem;
  border-bottom: 1px solid var(--primary-200);
  background: linear-gradient(135deg, var(--primary-50) 0%, rgba(255, 255, 255, 0.8) 100%);
}

.card-body {
  padding: 2rem;
}

.card-footer {
  padding: 1.5rem 2rem;
  border-top: 1px solid var(--primary-200);
  background: var(--primary-50);
}

/* Professional Button System */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.75rem 1.5rem;
  border: 1px solid transparent;
  border-radius: var(--radius-md);
  font-size: 0.875rem;
  font-weight: 500;
  line-height: 1.25rem;
  text-decoration: none;
  cursor: pointer;
  outline: none;
  box-shadow: var(--shadow-sm);
  font-family: inherit;
  white-space: nowrap;
  user-select: none;
}

.btn:focus-visible {
  outline: 2px solid var(--accent-blue);
  outline-offset: 2px;
}

.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.btn-sm {
  padding: 0.5rem 1rem;
  font-size: 0.75rem;
}

.btn-lg {
  padding: 1rem 2rem;
  font-size: 1rem;
}

.btn-primary {
  background: linear-gradient(135deg, var(--accent-blue) 0%, #1d4ed8 100%);
  color: white;
  border-color: var(--accent-blue);
}

.btn-primary:hover {
  background: linear-gradient(135deg, #1d4ed8 0%, #1e40af 100%);
  box-shadow: var(--shadow-md);
}

.btn-secondary {
  background: white;
  color: var(--primary-700);
  border-color: var(--primary-300);
}

.btn-secondary:hover {
  background: var(--primary-50);
  border-color: var(--primary-400);
  box-shadow: var(--shadow-md);
}

.btn-success {
  background: linear-gradient(135deg, var(--accent-green) 0%, #059669 100%);
  color: white;
  border-color: var(--accent-green);
}

.btn-success:hover {
  background: linear-gradient(135deg, #059669 0%, #047857 100%);
  box-shadow: var(--shadow-md);
}

.btn-warning {
  background: linear-gradient(135deg, var(--accent-orange) 0%, #d97706 100%);
  color: white;
  border-color: var(--accent-orange);
}

.btn-warning:hover {
  background: linear-gradient(135deg, #d97706 0%, #b45309 100%);
  box-shadow: var(--shadow-md);
}

.btn-danger {
  background: linear-gradient(135deg, var(--accent-red) 0%, #dc2626 100%);
  color: white;
  border-color: var(--accent-red);
}

.btn-danger:hover {
  background: linear-gradient(135deg, #dc2626 0%, #b91c1c 100%);
  box-shadow: var(--shadow-md);
}

/* Professional Form Inputs */
.form-group {
  margin-bottom: 1.5rem;
}

.form-label {
  display: block;
  margin-bottom: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--primary-700);
}

.form-input, .form-textarea, .form-select {
  width: 100%;
  padding: 0.75rem 1rem;
  border: 1px solid var(--primary-300);
  border-radius: var(--radius-md);
  background: white;
  font-size: 0.875rem;
  line-height: 1.25rem;
  color: var(--primary-900);
  box-shadow: var(--shadow-sm);
}

.form-input:focus, .form-textarea:focus, .form-select:focus {
  outline: none;
  border-color: var(--accent-blue);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.form-input:disabled, .form-textarea:disabled, .form-select:disabled {
  background: var(--primary-100);
  color: var(--primary-500);
  cursor: not-allowed;
}

.form-textarea {
  resize: vertical;
  min-height: 5rem;
}

/* Professional Grid System */
.grid {
  display: grid;
  gap: 1.5rem;
}

.grid-cols-1 { grid-template-columns: repeat(1, 1fr); }
.grid-cols-2 { grid-template-columns: repeat(2, 1fr); }
.grid-cols-3 { grid-template-columns: repeat(3, 1fr); }
.grid-cols-4 { grid-template-columns: repeat(4, 1fr); }

/* Professional Spacing */
.space-y-4 > * + * { margin-top: 1rem; }
.space-y-6 > * + * { margin-top: 1.5rem; }
.space-x-4 > * + * { margin-left: 1rem; }

/* Professional Flexbox Utilities */
.flex { display: flex; }
.flex-col { flex-direction: column; }
.flex-wrap { flex-wrap: wrap; }
.items-center { align-items: center; }
.items-start { align-items: flex-start; }
.items-end { align-items: flex-end; }
.justify-center { justify-content: center; }
.justify-between { justify-content: space-between; }
.justify-end { justify-content: flex-end; }
.gap-4 { gap: 1rem; }
.gap-6 { gap: 1.5rem; }

/* Professional Scrollbar */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: var(--primary-100);
  border-radius: var(--radius-md);
}

::-webkit-scrollbar-thumb {
  background: var(--primary-400);
  border-radius: var(--radius-md);
  border: 2px solid var(--primary-100);
}

::-webkit-scrollbar-thumb:hover {
  background: var(--primary-500);
}

/* Professional Modal Overlay */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(15, 23, 42, 0.5);
  backdrop-filter: blur(8px);
  z-index: 50;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}

.modal-content {
  background: white;
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-xl);
  max-width: 100%;
  max-height: 90vh;
  overflow-y: auto;
}

/* Professional Typography */
.text-xs { font-size: 0.75rem; line-height: 1rem; }
.text-sm { font-size: 0.875rem; line-height: 1.25rem; }
.text-base { font-size: 1rem; line-height: 1.5rem; }
.text-lg { font-size: 1.125rem; line-height: 1.75rem; }
.text-xl { font-size: 1.25rem; line-height: 1.75rem; }
.text-2xl { font-size: 1.5rem; line-height: 2rem; }
.text-3xl { font-size: 1.875rem; line-height: 2.25rem; }

.font-medium { font-weight: 500; }
.font-semibold { font-weight: 600; }
.font-bold { font-weight: 700; }

.text-primary { color: var(--primary-600); }
.text-secondary { color: var(--primary-500); }
.text-muted { color: var(--primary-400); }
.text-danger { color: var(--accent-red); }
.text-success { color: var(--accent-green); }
.text-warning { color: var(--accent-orange); }

/* Professional Status Badges */
.badge {
  display: inline-flex;
  align-items: center;
  padding: 0.25rem 0.75rem;
  border-radius: 9999px;
  font-size: 0.75rem;
  font-weight: 500;
  line-height: 1rem;
}

.badge-primary {
  background: #dbeafe;
  color: #1e40af;
}

.badge-success {
  background: #dcfce7;
  color: #16a34a;
}

.badge-warning {
  background: #fef3c7;
  color: #d97706;
}

.badge-danger {
  background: #fee2e2;
  color: #dc2626;
}

.badge-info {
  background: #e0f2fe;
  color: #0369a1;
}

/* Professional Timeline Styles */
.timeline {
  position: relative;
}

.timeline-item {
  background: white;
  border: 1px solid var(--primary-200);
  border-radius: var(--radius-lg);
  margin-bottom: 0.5rem;
  box-shadow: var(--shadow-sm);
  overflow: hidden;
}

.timeline-item:hover {
  box-shadow: var(--shadow-md);
  border-color: var(--primary-300);
}

.timeline-item.has-event {
  border-left: 4px solid var(--accent-blue);
  background: linear-gradient(135deg, #f8fafc 0%, #f1f5f9 100%);
}

.timeline-item.has-todo {
  border-left: 4px solid var(--accent-green);
  background: linear-gradient(135deg, #f0fdf4 0%, #dcfce7 100%);
}

.timeline-time {
  font-weight: 600;
  color: var(--primary-600);
  font-size: 0.875rem;
  width: 4rem;
}

.timeline-content {
  flex: 1;
  padding: 1rem;
}

.event-card {
  background: linear-gradient(135deg, var(--accent-blue) 0%, #1d4ed8 100%);
  color: white;
  padding: 0.75rem 1rem;
  border-radius: var(--radius-md);
  margin-bottom: 0.5rem;
  box-shadow: var(--shadow-md);
}

.event-card:last-child {
  margin-bottom: 0;
}

.todo-item {
  background: white;
  border: 1px solid var(--primary-200);
  padding: 0.5rem 0.75rem;
  border-radius: var(--radius-md);
  margin-bottom: 0.25rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.todo-item.completed {
  opacity: 0.6;
  text-decoration: line-through;
}

/* Professional Mobile Responsiveness */
@media (max-width: 768px) {
  .container {
    padding: 1rem;
  }
  
  .card-header {
    padding: 1rem 1.5rem;
  }
  
  .card-body {
    padding: 1.5rem;
  }
  
  .grid-cols-2 {
    grid-template-columns: 1fr;
  }
  
  .flex-col-mobile {
    flex-direction: column;
  }
  
  .gap-4 {
    gap: 0.75rem;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 0.75rem;
  }
  
  .text-2xl {
    font-size: 1.25rem;
    line-height: 1.75rem;
  }
  
  .text-xl {
    font-size: 1.125rem;
    line-height: 1.75rem;
  }
}
